/* Home page styles */
.home-content {
    max-width: 100%;
}

.home-title-section {
    margin-bottom: 2.5em;
    text-align: center;
}

.home-main-title {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    margin-bottom: 0.5em;
    line-height: 1.3;
}

.title-underline {
    width: 100px;
    border: 0;
    border-top: 3px solid #3498db;
    margin: 0 auto;
}

.home-section {
    margin-bottom: 2.5em;
}

.section-title {
    font-size: 1.6em;
    font-weight: 600;
    color: #2c3e50;
    margin-bottom: 0.8em;
    font-style: italic;
}

.home-text {
    font-size: 1.1em;
    line-height: 1.8;
    color: #333;
    text-align: justify;
    
    em {
        font-style: italic;
    }
}

/* News divider */
.news-divider {
    margin: 3em 0 2em 0;
    border: 0;
    border-top: 1px solid #e0e0e0;
}

/* Group logo */
#logo {
    font-size: 1.8em;
    font-weight: 700;
    color: #333;
    margin: 0.5em 0;
    line-height: 1.2;
}

.logo-text {
    text-decoration: none;
    
    &:hover {
        text-decoration: none;
    }
    
    h1 {
        margin: 0;
    }
}

/* Front page news. */
ul.news {

    .date {
        color: $gray;
        font-weight: bold;
    }

    > li {
        margin-top: 1em;
    }
}

@media (min-width: 576px) {
    .project-people { column-count: 2 }
    .card-columns   { column-count: 2 }
}

@media (min-width: 768px) {
    .role.grad      { column-count: 2 }
    .project-people { column-count: 3 }
    .card-columns   { column-count: 3 }
}

.title img {
    max-width: 50%;
}

section {
    margin-bottom: 2em;
}

/* People section with underline for h3 titles */
section {
    h3 {
        padding-bottom: 0.5em;
        border-bottom: 2px solid $light-gray;
        margin-bottom: 1.5em;
    }
}

/* Co-lead PIs side by side layout */
.co-leads-container {
    display: flex;
    justify-content: space-between;
    gap: 2em;
    margin-bottom: 2em;
    
    @media (max-width: 768px) {
        flex-direction: column;
    }
}

.co-lead-card {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .person-photo {
        flex-shrink: 0;
        margin-bottom: 1.5em;
        
        img {
            width: 200px;
            height: 200px;
            object-fit: cover;
            border-radius: 8px;
        }
    }
    
    .person-info {
        text-align: center;
    }
}

/* Team members horizontal layout */
.team-members-container {
    display: flex;
    flex-wrap: wrap;
    gap: 2em;
    margin-bottom: 2em;
    justify-content: flex-start;
    
    @media (max-width: 768px) {
        flex-direction: column;
    }
}

.team-member-card {
    flex: 0 1 calc(33.333% - 1.5em);
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 250px;
    
    @media (max-width: 1024px) {
        flex: 0 1 calc(50% - 1em);
    }
    
    @media (max-width: 768px) {
        flex: 1 1 100%;
    }
    
    .person-photo {
        flex-shrink: 0;
        margin-bottom: 1.5em;
        
        img {
            width: 200px;
            height: 200px;
            object-fit: cover;
            border-radius: 8px;
        }
    }
    
    .person-info {
        text-align: center;
    }
}

/* People full row layout */
.person-full-row {
    display: flex;
    align-items: flex-start;
    margin-bottom: 2.5em;
}

.person-photo {
    flex-shrink: 0;
    margin-right: 2em;
    
    img {
        width: 200px;
        height: 200px;
        object-fit: cover;
        border-radius: 8px;
    }
}

.person-info {
    flex-grow: 1;
}

.person-name-large {
    color: #2c3e50; // Deep blue for names - professional and authoritative
    font-size: 1.5em;
    font-weight: 600;
    margin-bottom: 0.3em;
    
    a {
        color: #2c3e50;
        text-decoration: none;
        
        &:hover {
            color: #3498db; // Lighter blue on hover
            text-decoration: underline;
        }
    }
}

.person-school {
    color: #333; // Black color for school
    font-size: 1.2em;
    font-weight: 500;
    font-style: italic; // Italic style
    margin-bottom: 0.8em;
}

.person-research {
    color: #333;
    font-size: 1.05em;
    line-height: 1.6;
    
    div {
        margin-bottom: 0.2em;
    }
}

/* Publications page styles */
.publications {
    .year-divider {
        margin: 2em 0 1em 0;
        border: 0;
        border-top: 1px solid $light-gray;
    }
    
    .pub-year {
        color: #800000; // Maroon color for years
        font-size: 1.5em;
        font-weight: bold;
        font-style: italic;
        margin-bottom: 1.5em;
    }
    
    .pub-item {
        margin-bottom: 1.8em;
        line-height: 1.5;
    }
    
    .pub-venue {
        font-weight: bold;
        margin-bottom: 0.3em;
    }
    
    .pub-title {
        font-weight: bold;
        margin-bottom: 0.3em;
        
        a {
            color: #3498db;
            text-decoration: none;
            
            &:hover {
                color: #2c3e50;
                text-decoration: underline;
            }
        }
    }
    
    .pub-authors {
        margin-bottom: 0.3em;
    }
    
    .pub-conference {
        margin-bottom: 0.3em;
        
        em {
            color: #666;
        }
    }
}

.profile {
    object-fit: cover; // makes sure it's cropped
    object-position: center; // center the image
    border-radius: 8px; // rounded square instead of circle
    background: white; // white background to avoid black edges
}

.noprofile {
    circle {
        fill: $light-gray;
    }
    text {
        font-weight: 700;
        fill: white;
    }
}

.card {
    border: 1px solid #e0e0e0; // Light gray border
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    transition: transform 0.2s, box-shadow 0.2s;
    border-top: 4px solid #3498db; // Blue top border
    background-color: #fff; // White background
    
    &:hover {
        transform: translateY(-4px);
        box-shadow: 0 4px 16px rgba(0,0,0,0.15);
    }
    
    .card-body {
        padding: 1.5em;
    }
    
    .card-title {
        font-size: 1.4em;
        font-weight: 700;
        color: #2c3e50;
        margin-bottom: 0.8em;
    }
    
    .card-text {
        color: #555;
        font-size: 1.05em;
        line-height: 1.6;
        margin-bottom: 1em;
    }
    
    a {
        color: #3498db;
        font-weight: 600;
        text-decoration: none;
        
        &:hover {
            color: #2c3e50;
            text-decoration: none;
        }
    }

    .profile {
        height: 40px;
        width: 40px;
        margin: 3px;
    }

    .card-footer { 
        padding: 0.5em;
        background-color: #f8f9fa;
        border-top: 1px solid #e9ecef;
    }
}

.person {

    margin-bottom: 10px;

    .profile {
        height: 70px;
        width: 70px;
    }

    .bio {
        color: #666;
    }

    .person-name {
        margin: 0;
    }
}

.person-without-image {
    margin: 0;

    .bio {
        margin-left: 1em;
    }
}

/* Contact page styles */
.contact-item {
    margin-bottom: 1.5em;
    line-height: 1.8;
    
    p {
        margin-bottom: 0.5em;
    }
    
    a {
        color: #2c3e50;
        text-decoration: none;
        
        &:hover {
            color: #3498db;
            text-decoration: underline;
        }
    }
}

.location-info {
    margin-bottom: 2em;
    line-height: 1.8;
    
    p {
        margin-bottom: 0.5em;
    }
}

.map-container {
    margin-top: 1.5em;
    
    p {
        margin-bottom: 1em;
    }
    
    iframe {
        box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    }
}

svg {
    // make the alignment just like img from bootstrap's reboot.scss
    vertical-align: middle;
}

/* Open Positions page styles */
.positions-content {
    max-width: 100%;
}

.positions-section {
    margin-bottom: 2.5em;
}

.position-type {
    font-size: 1.4em;
    font-weight: 600;
    color: #2c3e50;
    margin-bottom: 0.8em;
}

.positions-text {
    font-size: 1.1em;
    line-height: 1.8;
    color: #333;
    text-align: justify;
}

.contact-section {
    margin-top: 3em;
    padding-top: 2em;
    border-top: 2px solid $light-gray;
}

.contact-info {
    display: flex;
    justify-content: space-between;
    gap: 3em;
    margin-top: 2em;
    
    @media (max-width: 768px) {
        flex-direction: column;
        gap: 2em;
    }
}

.contact-person {
    flex: 1;
    padding: 1.5em;
    background-color: #f8f9fa;
    border-radius: 8px;
    border-left: 4px solid #3498db;
    
    p {
        margin-bottom: 0.5em;
        line-height: 1.6;
    }
    
    a {
        color: #3498db;
        text-decoration: none;
        
        &:hover {
            color: #2c3e50;
            text-decoration: underline;
        }
    }
}
